@import "../../global.scss";

.intro{
    background-color: $secondaryColor;
    display: flex;

    @include mobile {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 600px;
            height: 900px;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile {
                align-items: flex-start;
            }

            img{
                width: 100%;
                padding-bottom: 50px;

                @include mobile {
                    //height: 50%;
                    width: 50%;
                }
            }
        }

    }

    .right{
        flex: 0.5;
        position: relative;
        font-family: Chalkduster, Comic Sans, cursive;
        .wrapper{
            width: 80%;
            height: 80%;
            padding-left: 50px;
            padding-right: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile {
                padding-left: 0;
                align-items: center;
                font-size: 20px;
            }

            h1{
                font-size: 40px;
                margin: 10px 0;

                @include mobile {                
                    font-size: 20px;
                }
            }
            h2{
                font-size: 30px;

                @include mobile {                
                    font-size: 20px;
                }
            }
            h3{
                font-size: 25px;
                color: #7FC8A9;
                

                span{
                    font-size: inherit;
                    color:#FA8072;

                    @include mobile {
                        font-size: 20px;
                        display: inline-block;
                        align-items: center;
                    }
                    
                }
                .ityped-cursor{
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }

        }
        a{
            position: absolute;
            bottom: 10px;
            left:40%;

            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}